.App {
  text-align: center;
}

body {
  
  margin: 0;
  padding: 0;
  background: #606C5D;
  font-family: "Noto Sans KR", sans-serif;
}

.wrapper {
  width: 350px;
  padding: 40px 40px 60px 40px;
  margin: 135px auto 0;
  background-color: #ffffff;
  box-shadow: 10px 10px 10px #1f1f1f;
}
.top_text{

  display: flex;
  align-items: center;
  justify-content: space-between;

}

.year{
  font-size: 0.8em;
  color: #373f35;
  font-family: 'PuradakGentleGothicR';
}
.text{
  font-family: 'PuradakGentleGothicR';
  font-size: 1.7rem;
  font-weight: bold;
  color:#ebb85f;
      text-decoration: overline;
    font-style: italic;
}
.textinput{
  font-family: 'S-CoreDream-3Light';
  width: 77%;
  height: 35px;
  border: none;
  border-bottom:  1px solid #7e7a7a;
  font-size: 0.9rem;
  background-color: #ffffff;
}

input[type=text]:focus {

outline: none;
}
input[type=checkbox]{
  width: 15px;
  height: 20px;

}
.checked{
  accent-color: darkcyan;
  width: 15px;
  height: 20px;
}
/* input[type=checkbox]:checked+ h4{
  color: #888;
  text-decoration: line-through;
} */

.list{
  font-size: 0.8em;
  font-family: 'S-CoreDream-3Light';
  border-bottom: 1px solid #eee;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: space-between;

}

.list h4{
  
  display: block;
  width: 75%;
  text-align: left;

}
.input{

  
  display: flex;
  align-items: center;
  justify-content: space-between;

}
button{
  cursor: pointer;
  border: none;
  background: none;
}
.sort,.plus{

  width: 38px;height: 25px;
}

.plus{
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
background-image: url("./plus.png");
}
.sort{
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
background-image: url("./sort.png");
}
.close{
  border: none;
  background: none;
  background-position: center;
  width: 38px;
  height: 28px;
  background-size: contain;
  background-repeat: no-repeat;
background-image: url("./close.png");

}

img{

  width: 100%;
  height: 100%;
}

@font-face {
  font-family: 'PuradakGentleGothicR';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.1/PuradakGentleGothicR.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'S-CoreDream-3Light';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}